@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  width: 100%;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.container {
  overflow: hidden;
}

/*   header  */
.header {
  margin-top: 0px;
  background-color: #ebedf0;
  opacity: 1;
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  width: 100%;
  border-bottom: #f3c009fc outset 10px;
}

.buttonGroup {
  display: grid;
  grid-template-rows: auto auto auto auto auto;
}

/* body */
.bgImg {
  height: 300px;
  background-repeat: no-repeat;
}

.img {
  border:#e79216;
  border-radius: 5pc;
  margin-left: 30px;
  padding-top: 5px;
  padding-left: 5px;
  width: 150px;
  height: 150px;
}

.button {
  color: #2d2928;
  font-size: 16px;
  border: none;
  text-transform: uppercase;
  font-weight: 800;
  cursor: pointer;
  background: none;
  height: 100%;
  align-items: center;
  text-align: left;
}

.button:hover {
  color: #e79216;
  background: none;
  text-transform: uppercase;
  cursor: pointer;
  font-size: 16px;
}

/* Footer */
.footer {
  height: 5vh;
  background-color: #ebedf0;
}

.footer>p {
  padding-top: 10px;
  margin-top: 15px;
  border-top: solid #555 1px;
  font-size: 14px;
  text-align: center;
}

/* desktop screen */
@media only screen and (min-width: 800px) {

  /*   header  */
  .header {
    margin-top: 0px;
    background-color: #ebedf0;
    opacity: 1;
    position: -webkit-sticky;
    position: sticky;
    display: grid;
    grid-template-columns: auto auto auto;
    width: 100%;
    border-bottom: #f3c009fc outset 10px;
  }

  .buttonGroup {
    grid-column: 2;
    display: grid;
    grid-template-columns: auto auto auto auto auto;
  }


  .button {
    color: #2d2928;
    font-size: 16px;
    border: none;
    text-transform: uppercase;
    margin: 20px;
    cursor: pointer;
    background: none;
  }

  /* body */
  .bgImg {
    height: 800px;
    background-repeat: no-repeat;
    color: aqua;
  }

  

}
.about {
    width: 100%;
    padding-left: 50px;
    clear: both;
    background-color: #ebedf0;
    overflow: hidden;
    background-size: cover;
}

.about>h1 {
    width: 90%;
    z-index: 0;
    margin: 3% auto;
    color: #555;
    z-index: 0;
    font-size: 18px;
}

.about>h2 {
    width: 90%;
    color: #555;
    margin: 2% auto;
    font-size: 14px;
}

/* desktop screen */
@media only screen and (min-width: 800px) {
    .about h1 {
        width: 80%;
        z-index: 0;
        margin: 3% auto;
        color: #555;
        z-index: 0;
        font-size: 22px;
    }

    .about h2 {
        width: 80%;
        color: #555;
        margin: 2% auto;
        font-size: 18px;
    }
}
/*location detail */
.locationDetail {
    width: 100%;
    padding-left: 2%;
    clear: both;
    background-color: #ebedf0;
    border-bottom: solid #d7d9dc 1px;
    overflow: hidden;
    background-size: cover;
}

.locationDetail h2 {
    text-align: center;
    font-size: 18px;
}

.locationDetail table {
    width: 98%;
    table-layout: fixed;
}

.locationDetail tbody td {
    color: #e79216;
    background-color: #2d2928;
    padding: 10px;
    margin: 15px;
    font-size: 16px;
}

/* desktop screen */
@media only screen and (min-width: 800px) {
    .locationDetail tbody td {
        color: #e79216;
        background-color: #2d2928;
        padding: 20px;
        margin: 20px;
        font-size: 20px;
    }

    .locationDetail h2 {
        text-align: center;
        font-size: 1.5em;
    }
}

.bookingDetail {
    display: grid;
    height: 100%;
    background-color: #ebedf0;
    overflow: hidden;
    background-size: cover;
    grid-gap:5px;
    gap:5px;
    grid-template-areas:
        'header header  header'
        'question question  phone '
        'question question  phone '
        'question question  email';
}

.bookingDetail h2 {
    margin:30px;
    grid-area: header;
    text-align: center;
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 40px;
}

.bookingDetail .questionList {
    grid-area: question;
    text-align: left;
    margin-left: 10%;
    font-size: 16px;
}

li {
    padding: 8px;
    margin-right: 10px;
}

.bookingDetail .contactPhone {
    grid-area: phone;
    display: flex;
}

.bookingDetail .contactEmail {
    grid-area: email;
    display: flex;
    padding-right:10px;
}

h4 {
    margin-top: -1px;
}

a:link {
    color: #2d2928;
}

a:hover {
    color: #e79216;
}

a:active {
    color: #2d2928;
}

@media only screen and (min-width: 800px) {
    .bookingDetail {
        display: grid;
        height: auto;
        background-color: #ebedf0;
        overflow: hidden;
        background-size: cover;
        grid-template-areas:
            'header header header'
            'menu  menu phone'
            'menu  menu email'
    }

    .bookingDetail h2 {
        grid-area: header;
        text-align: center;
    }

    .bookingDetail .questionList {
        width: 80%;
        grid-area: menu;
        text-align: left;
        margin-left: 10%;
        font-size: 18px;
    }

    .bookingDetail .contactPhone {
        grid-area: phone;
        display: grid;
        grid-template-columns: auto auto;
    }

    .bookingDetail .contactEmail {
        margin-left: 10%;
        display: grid;
        grid-template-columns: auto auto;
        grid-area: email;
    }
}
